// User API endpoints
import { apiClient } from './client';
export const userApi = {
    /**
     * POST /api/User/register
     * Registers a new user with a unique email, name, and password.
     */
    async register(request) {
        return apiClient.post('/User/register', request);
    },
    /**
     * POST /api/User/authenticate
     * Authenticates a user with their email and password.
     */
    async authenticate(request) {
        return apiClient.post('/User/authenticate', request);
    },
    /**
     * POST /api/User/deactivate
     * Sets a user's account status to INACTIVE.
     */
    async deactivate(request) {
        return apiClient.post('/User/deactivate', request);
    },
    /**
     * POST /api/User/changePassword
     * Changes a user's password after verifying the old password.
     */
    async changePassword(request) {
        return apiClient.post('/User/changePassword', request);
    },
    /**
     * POST /api/User/reactivate
     * Reactivates an inactive user account and updates their password.
     */
    async reactivate(request) {
        return apiClient.post('/User/reactivate', request);
    },
    /**
     * POST /api/User/all
     * Returns all user documents in the system.
     */
    async all() {
        return apiClient.post('/User/all', {});
    },
};
