// Category API endpoints
import { apiClient } from './client';
export const categoryApi = {
    /**
     * POST /api/Category/create
     * Creates a new category for a given user with a specified name.
     */
    async create(request) {
        return apiClient.post('/Category/create', request);
    },
    /**
     * POST /api/Category/rename
     * Renames an existing category for a given user.
     */
    async rename(request) {
        return apiClient.post('/Category/rename', request);
    },
    /**
     * POST /api/Category/delete
     * Deletes a category and its associated metrics, provided it's safe to delete.
     */
    async delete(request) {
        return apiClient.post('/Category/delete', request);
    },
    /**
     * POST /api/Category/setMetricTotal
     * Creates or updates a category metric's total for a specific period.
     */
    async setMetricTotal(request) {
        return apiClient.post('/Category/setMetricTotal', request);
    },
    /**
     * POST /api/Category/getCategoryNamesAndOwners
     * Retrieves a list of all category names and their owners.
     */
    async getCategoryNamesAndOwners() {
        const raw = await apiClient.post('/Category/getCategoryNamesAndOwners', {});
        // Helper to extract string owner id from various shapes
        const extractOwnerId = (v) => {
            if (v == null)
                return null;
            if (typeof v === 'string')
                return v;
            if (typeof v === 'object') {
                if (typeof v.value === 'string')
                    return v.value;
                if (typeof v.value === 'object' && typeof v.value.value === 'string')
                    return v.value.value;
            }
            // fallback to string coercion
            try {
                return String(v);
            }
            catch {
                return null;
            }
        };
        const extractCategoryId = (r) => {
            if (!r)
                return undefined;
            if (typeof r.category_id === 'string')
                return r.category_id;
            if (r._id && typeof r._id === 'string')
                return r._id;
            return undefined;
        };
        return (raw || []).map((r) => ({
            category_id: extractCategoryId(r),
            name: r.name,
            owner_id: extractOwnerId(r.owner_id) || ''
        }));
    },
    /**
     * POST /api/Category/getMetric
     * Retrieves a specific category metric document for a given owner, category, and period.
     */
    async getMetric(request) {
        return apiClient.post('/Category/getMetric', request);
    },
    /**
     * POST /api/Category/listMetrics
     * Lists all category metrics for a given owner and category, sorted by period start date.
     */
    async listMetrics(request) {
        return apiClient.post('/Category/listMetrics', request);
    },
};
