// Transaction API endpoints
import { apiClient } from './client';
export const transactionApi = {
    /**
     * POST /api/Transaction/import_transactions
     * Parses a CSV file and converts its rows into new, unlabeled transactions owned by a user.
     */
    async importTransactions(request) {
        return apiClient.post('/Transaction/import_transactions', request);
    },
    /**
     * POST /api/Transaction/mark_labeled
     * Sets a transaction's status to LABELED after verifying ownership.
     */
    async markLabeled(request) {
        return apiClient.post('/Transaction/mark_labeled', request);
    },
    /**
     * POST /api/Transaction/getTransaction
     * Retrieves a single transaction document by its ID.
     */
    async getTransaction(request) {
        return apiClient.post('/Transaction/getTransaction', request);
    },
    /**
     * POST /api/Transaction/list_all
     * Returns all transaction documents in the system.
     */
    async listAll() {
        return apiClient.post('/Transaction/list_all', {});
    },
};
